@media only screen and (min-width: 600px) {
  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    padding-block: 3rem;
    margin-left: 5rem;
    margin-right: 5rem;
  }
}

@media only screen and (max-width: 600px) {
  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
p {
  text-align: center;
}
